@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}ul{list-style:none}a{color:black;text-decoration:none}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

a{
    font-family: Nunito;
    font-size: 16px;
    line-height: 28px;
    font-weight: 400;
}

*{
    box-sizing: border-box;
}

.container{
    max-width: 1024px;
    height: 60px;
    margin: auto;
    overflow: hidden;
    background: red;
    padding: 0 60px;
}

.header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2;
    margin: 0 auto;
    width: 904px;
    height: 100px;
    background-color: grey;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 100%;
}

ul {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
}

.logo{
    margin-top: 4px;
}

.nav-menu li{
    display: inline-block;
    margin-left: 30px;
}

.nav-menu li.move {
    display: inline-block;
    margin-left: 34px;
}


